import None from '../../public/noNumber'
import Loading from '../../public/Loading'
import LoadingImg from '../../public/loadingImg'
  export default {
      name: "noComplete",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          orderList:[],
          total:0,
          payFor:'',
          goPay:false,
          orderId:"",
          form:{
            token: localStorage.getItem('token'),
            order_id:"",
            pay_type:''
          },
          isWeixin:false,
          isPay:'',
          payStatus:'',
          timeOut:'',
          isLoading:false,
          payList:[
            {img: '../../../static/img/static/aliPay.png',name:'支付宝'},
            {img: '../../../static/img/static/weChat.png',name:'微信'},
          ],
          clickIndex: -1,
          isBank:false,
          payPrice:''
        }
    },
    components:{
      Loading,
      None,
      LoadingImg
    },
    watch:{
      payFor:function(){
        if(this.payFor == 2){
          this.form.order_id = this.orderId
          this.form.pay_type = this.payFor
        }
      }
    },
    methods:{
        closePay(){
          this.goPay = false
          this.clickIndex = -1
        },
      checkedBank(){

      },
      checkOrder(item,index){
        this.$router.push({
          name: 'orderFor',
          query:{
            order_id: item.order_id,
            type: 2
          }
        })
      },
      handleCurrentChange(val){
        this.getOrderList(val)
      },
      getOrderList(num){
        this.$http.post(this.api + '/OrderLTwo',{
          token: localStorage.getItem('token'),
          page: num,
          flag: 0
        })
          .then(res=>{
            if(res.data.code == 1){
              this.orderList =  res.data.data.data
              this.total = res.data.data.total
              console.log(this.orderList)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getOrderList(num)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      goFor(item,index){
        this.goPay = !this.goPay
        this.orderId = item.order_id
        this.payPrice = item.total_price
      },
      goPayM(){
        if(this.payFor == 1){
          this.isLoading = true
          this.$http.post(this.api + '/Pay',{
            token: localStorage.getItem('token'),
            order_id: this.orderId,
            pay_type: this.payFor
          })
            .then(res=>{
              if(res.data.code == 1){
                this.goPay = false
                this.isWeixin = true
                this.isPay = 0
                this.isWeixin = true
                document.getElementById('weixin').innerHTML = res.data.data.url
                this.isLoading = false
                this.setTime()
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.goPayM()
                    })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }else{
          this.$message({
            type: 'info',
            message:'选择支付方式'
          })
        }
      },
      setTime(){
        let a = 0
        this.timeOut = setInterval(()=>{
          if(a < 300){
            this.$http.post(this.api + '/OrderIsPay',{
              token: localStorage.getItem('token'),
              order_id: this.orderId
            })
              .then(res=>{
                if(res.data.code == 1){
                  if(res.data.data.pay_status == 1){
                    this.isPay = 1
                    this.payStatus = 1
                    clearInterval(this.timeOut);
                  }
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.setTime()
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
            a+=1;
          }else{
            clearInterval(this.timeOut);
            this.isPay = 1
            this.payStatus = 2
          }
        },1000)
      },
      payClose(){
        this.isWeixin = false
        this.isPay = ''
        this.payStatus = ''
      },
      closeWx(){
        this.isWeixin = false
        clearInterval(this.timeOut)
      },
      choosePay(item,index){
        this.clickIndex = index
        if(index == 0){
          this.payFor = 2
        }else if(index == 1){
          this.payFor = 1
        }else{
          this.payFor = 3
        }
      },
      addBank(){
        this.isBank = !this.isBank
      }
    },
    mounted() {
     this.getOrderList(1)
    },
    destroyed(){
      clearInterval(this.timeOut);
    }
  }



